import React from 'react'
import { useNavigate } from 'react-router-dom'
import { footerSend, facebook, instagram, linkedIn, } from '../../assets'
import './footer.css'

export default function Footer() {
    const navigate = useNavigate()
    return (
        <div className='cactus-footerTopView'>
            <div className='cactus-footerContainer' >
                <div className='cactus-footerItemTopView'>
                    <h1> propos de nous</h1>
                    {/* <h4>Logo</h4> */}
                    <p>Faites de chaque cadeau un souvenir inoubliable en consultant notre boutique de cadeaux personnaliss. Que ce soit pour un anniversaire, un mariage, une fte de naissance, un EVG ou tout simplement pour montrer votre amour, nos cadeaux sur mesure sont la solution parfaite. Choisissez parmi une varit d'options pour crer des articles uniques qui laisseront une impression durable.</p>
                    <div className='cactus-footerSocialView'>
                        <img src={facebook} alt='' />
                        <img src={linkedIn} alt='' />
                        <img src={instagram} alt='' />
                    </div>
                </div>
                <div className='cactus-footerItemTopView margin100'>
                    <h1>Site Web</h1>
                    <h2 onClick={() => navigate('/')}>Home</h2>
                    <h2 onClick={() => navigate('/aboutus')}> propos de nous </h2>
                    <h2 onClick={() => navigate('/poster')}>Posters</h2>
                    <h2 onClick={() => navigate('/privacypolicy')}> Politique de confidentialit </h2>
                    <h2 onClick={() => navigate('/terms')}>Termes et conditions</h2>
                </div>
                <div className='cactus-footerItemTopView'>
                    <h1 style={{ marginBottom: 5 }}>Aide</h1>
                    <h2 onClick={() => navigate('/contactus')}>Contactez-nous</h2>
                    <div className='cactus-footer-input_text_view'>
                        <input placeholder='Email address' />
                        <img src={footerSend} alt={''} />
                    </div>
                </div>
            </div>
            <h3>Copyrights  Cactus Moments 2023 | Powered by TX Dynamics</h3>
        </div>
    )
}
